@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  /* background-color: #18181c; */
  color: #18181c
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
    /* background-color: #18181c; */
    color: #18181c
    
  }
 

.header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    /* margin-top: 40px; */
    height: 71px;
    box-shadow: 0 0 3px 3px gray;
    background: linear-gradient(rgb(142, 228, 179),white);


}
.brand{
    padding-top: 14px;
    font-size: 24px;
    color: black;
    font-family: url('https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap'); 
    text-transform: uppercase;
}
.inputfield{
    padding-left: 16px;
    /* padding-top: 12px; */
    margin-top: 10px;
    font-size: 14px;
    width: 216px;
    height: 40px;
    border-radius: 5px;
    box-shadow: 0 0 3px 1px black;
    color: #e2e2e2;

}
.coinsContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 30px;
    color: #fff;
  }
  

@media(max-width:520px){
    .header{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;


    }
    .brand{ 
         padding-bottom: 16px; 
        
    }
    .inputfield{
        margin-bottom: 9px;
    }
}

.cryptoCoin {
    overflow: hidden;
    text-align: center;
    border-radius: 1rem;
    position: relative;
    width: 320px;
    margin: 2rem;
    box-shadow: 0 3px 3px rgba(0,0,0,0.5);
    background:linear-gradient(rgb(241, 227, 227),white);
}

.coinLogo {
    width: 8rem;
    margin-top: 1.2rem;
    height: 40px;
    width: 40px;
    -webkit-clip-path: circle(60px at center);
            clip-path: circle(60px at center);
}

.coinNameWrap {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 4px;
    font-weight: 700;
}

.coinName {
    font-weight: bold;
    font-size: 1.5rem;
}

.coinSymbol {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    padding-left: 8px;
}

.priceContainerUP {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #11d811;
}

.priceContainerDOWN {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #f00606;
}

svg {
    background-color: transparent;
}

.priceChange {
    background-color: transparent;
    padding-left: 10px;
    text-shadow: 0 0 14px black;
}

.coinPrice {
    width: auto;
    background-color: transparent;
}

.coinMarketcap {
    width: auto;
    padding-top: 8px;
    background-color: transparent;
}

.coinVolume {
    width: auto;
    padding-top: 6px;
    padding-bottom: 8px;
    background-color: transparent;
}

.foot{
    text-transform: uppercase;
    background-color: rgb(233, 164, 198);
}
